import React from "react";
import {Card, Form, Input, Button, Icon, Breadcrumb} from "antd";
import axios from "axios";
import {ADD_PROBLEMS_IDENTIFIED} from "../../components/api";
import {Link} from "react-router-dom";

const FormItem = Form.Item;

const formItemLayout = {
  labelCol: {
    xs: {span: 24},
    sm: {span: 5},
  },
  wrapperCol: {
    xs: {span: 24},
    sm: {span: 18},
    md: {span: 16},
    lg: {span: 12},
  },
};

class CreateProject extends React.Component {

  constructor(props) {
    super(props);

    this.checkValidation("addProblems");

    this.state = {
      loginId: localStorage.getItem("starkLoginId"),
      uniqueId: props.match.params.projectId
    }
  }

  checkValidation(redirectTo){
    if(localStorage.getItem("starkLoginId") && localStorage.getItem("clear")){
      if(localStorage.getItem("clear")==="ok"){
        localStorage.removeItem("clear");
      }
      else{
        localStorage.setItem("redirectTo",redirectTo);
        return window.location.href = '/stark/login';
      }
    }
    else{
      localStorage.setItem("redirectTo",redirectTo);
      return window.location.href = '/stark/login';
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    var self = this;
    this.props.form.validateFields((err, values) => {
      if (!err) {
              axios.post(ADD_PROBLEMS_IDENTIFIED, {
              projectId: self.state.uniqueId,
              problem: values.problemName
              })
              .then(function (response) {
                console.log(response);
                if(response.status===200){
                  self.props.form.resetFields();
                  if(response.data==="Added"){
                    alert("Label added!");
                  }
                  else if(response.data==="Limit exceeds"){
                    alert("Limit to add problems has exceeded!");
                  }
                  else if(response.data==="Already present"){
                    alert("Label is already present in collection!");
                  }
                  else if(response.data==="Invalid problem"){
                    alert("Label contains invalid characters! Please enter characters (a-z) only!");
                  }
                }
              })
              .catch(function (error) {
                console.log(error);
              });
      }
    });
  };

  render() {
  const { getFieldDecorator } = this.props.form;
  return (
    <Card className="gx-card" title="Problem Identification">
    <Breadcrumb>
    <Breadcrumb.Item>Home</Breadcrumb.Item>
    <Breadcrumb.Item><span className="gx-link">Problem Identification</span></Breadcrumb.Item>
    </Breadcrumb>
    <br/><br/>
    <h4>Here you can add a maximum of 10 lables you wish to identify. (Examples - "football" or "person" or "cat")</h4>
    <br/>
      <Form  onSubmit={this.handleSubmit}>
      <FormItem
      label = "Add A Problem"
      {...formItemLayout}>
        {getFieldDecorator('problemName', {
          rules: [{ required: true, message: 'Please enter a label you wish to identify!' }],
        })(
          <Input
            prefix={<Icon type="project" style={{ color: 'rgba(0,0,0,.25)' }} />}
            placeholder="Enter a label you want to identify" id="problemName" autoComplete="off"
          />,
        )}
      </FormItem>

        <Button block type="primary" shape="round" htmlType="submit" className="login-form-button">
<Icon type="right-circle" theme="filled"/>Add Problem To Collection!
          </Button>
      </Form>
      <Link to={"/stark/uploads/"+this.state.uniqueId}>
        <Button block type="primary" shape="round"> <Icon type="right-circle" theme="filled"/>Move To Data Collection!</Button>
      </Link>
    </Card>
  );
}
}

const WrappedForm = Form.create({ name: 'new_project_form' })(CreateProject);

export default WrappedForm;
